<template>
	<div>
		<div style="float: right;margin: 10px 20px 20px 0;">
			<el-row>
				<el-col>
					<el-button @click="handleAdd" size="small" plain>添加用户</el-button>
				</el-col>
			</el-row>
		</div>


		<el-table :data="tableData" style="width: 100%" max-height="250">
			<el-table-column fixed prop="createDate" label="日期">
			</el-table-column>
			<el-table-column prop="remark" label="备注">
			</el-table-column>
			<el-table-column prop="userName" label="身份证名">
			</el-table-column>
			<el-table-column prop="phone" label="手机号">
			</el-table-column>
			<el-table-column prop="cardType" label="卡类型">
			</el-table-column>
			<el-table-column fixed="right" label="操作" width="120">
				<template slot-scope="scope">
					<el-button @click.native.prevent="stop(scope.$index, tableData)" type="text" size="small">
						{{tableData[scope.$index].isActive ? '停止' : '开启'}}
					</el-button>

					<el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small">
						移除
					</el-button>
				</template>
			</el-table-column>
		</el-table>

		<el-dialog title="添加用户" :visible.sync="showAdd" width="650px" append-to-body>
			<el-form ref="form" :model="form" :rules="rules" label-width="120px" style="margin: 0px 0px 70px 0px;">

				<div style="margin-bottom: 35px;width: 60%;">
					<el-input v-model="form.remark" maxlength="25" placeholder="请输入备注" clearable>
						<template slot="prepend">备注</template>
					</el-input>
				</div>

				<div>
					<el-input type="textarea" :rows="10" v-model="form.cookie" placeholder="请输入cookie" />
				</div>

			</el-form>

			<div>
				<el-button @click="submitForm" type="primary" size="medium" plain style="margin-right: 15px;">确 定
				</el-button>
				<el-button @click='form.remark= "";form.cookie=""' type="info" size="medium" plain>重 置</el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				form: {},
				rules: {},
				showAdd: false,
				tableData: [
				]
			};
		},
		created() {
			this.getList();
		},
		methods: {
			deleteRow(index, rows) {
				this.remove(index, rows);
			},

			stop(index, rows) {
				this.$http.get("/user/stop/" + rows[index].cardId).then(res => {
					if (res.data.code == 200) {
						this.success(res.data)
						this.getList()
					} else {
						this.error(res.data)
					}

				})

			},

			remove(index, rows) {
				this.$http.get("/user/remove/" + rows[index].cardId).then(res => {
					if (res.data.code == 200) {
						this.success(rows[index].cardId)
						rows.splice(index, 1)
					} else {
						this.error(res.data)
					}
				})
			},

			success(s) {
				this.$notify({
					title: '操作通知',
					message: s.msg,
					type: 'success',
					duration: 1500,
					showClose: true
				});
			},

			error(e) {
				if (e.data != null) {
					this.$notify({
						title: e.msg,
						message: e.data,
						type: 'error',
						duration: 4000,
						showClose: true
					})
				} else {
					this.$notify({
						title: e.msg,
						message: e.data,
						type: 'error',
						duration: 4000,
						showClose: true
					})
				}

			},

			getList() {
				this.$http.get('/user/toList').then(res => {
					if (res.data.code == 200) {

						if (res.data.data.length < 1) {

							return null;
						}
						this.tableData = res.data.data;
					} else {
						this.error(res.data)
					}
				});
			},

			handleAdd() {
				this.reset();
				this.showAdd = true;
			},

			handleStop(row) {
				this.$http.get('/del', { url: row.url }).then(res => {
					if (res.data.code == 200) {
						this.$message.success(res.data.msg);
						this.getList();
					}
				});
			},

			submitForm() {
				const loading = this.$loading({
					lock: true,
					text: '拼命处理中...',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});

				this.$http.post('/user/add', this.form).then(res => {
					loading.close();
					if (res.data.code == 200) {
						this.success(res.data)
						this.showAdd = false;
						this.getList();
					} else {
						this.error(res.data)
					}

				});
			},


			// 表单重置
			reset() {
				this.form = {
					id: undefined,
					num: undefined,
					remark: undefined
				};
			}
		}
	};
</script>

<style></style>